.contenedor-botones-operadores {
  display: flex;
  flex-direction: column;
  width: 25%;
}

.boton-operador {
  flex: 1 1;
  height: 100%;
  cursor: pointer;
  user-select: none;
  color: #252422;
  font-size: 30px;
  background-color: #EB5E28;
}

.boton-operador:active {
  background-color: #CCC5B9;
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
